import React from "react";
import CardExtras from '../CardExtras'
import { SubtitleCont, ContentSection, Container} from "./styles";

const CardExtraScreen= () =>{
    return(
        <ContentSection>
            <Container id='t18'>
                <SubtitleCont><b>Extracurriculares</b></SubtitleCont>
                <CardExtras title = "ADA - Projetos em Engenharia de Computao"  text = "Parabns por ter entrado no melhor curso de Engenharia de Computao do Brasil!Se voc quer desde o incio do curso trabalhar em projetos reais onde voc pode aplicar conhecimentos vistos em sala de aula, venha participar da ADA!A ADA   o grupo que realiza projetos no escopo do nosso curso e tem como objetivo proporcionar o aprendizado e o crescimento dos alunos do campus por meio da soluo de problemas utilizando o conhecimento de Engenharia de Computao.O grupo foi criado por pessoas da Eng Comp, ento sabemos como as coisas funcionam. Para participar no precisa ter nenhum conhecimento prvio em eletrnica ou programao, contamos apenas com sua vontade de aprender e sua determinao!Venha fazer parte desse incrvel grupo e tornar sua experincia no CAASO ainda melhor!"url = "img1"></CardExtras>
                <CardExtras title = "Atltica CAASO"  text = "A Atltica CAASO foi fundada em 1954, um ano depois do Centro Acadmico Armando de Salles Oliveira. Tendo o porco como nosso smbolo, somos o grupo do campus responsvel pela promoo e incentivo ao esporte. Por meio da organizao de torneios universitrios e festas, propiciamos a integrao dos alunos do CAASO. Nossa atltica  dividida em cinco reas, sendo elas, Esportes; Eventos; Administrativo; Marketing e Social. Alm disso, atualmente a atltica conta com mais de 500 atletas divididos em mais de 40 modalidades diferentes."url = "img2"></CardExtras>
                <CardExtras title = "BAJA - Projetos em Engenharia de Computao"  text = "O projeto  um desafio aos estudantes de engenharia de todo o mundo de desenvolver e construir um prottipo off-road, normatizado internacionalmente, passando por todas as etapas do desenvolvimento de um produto, aplicando, assim, os conhecimentos adquiridos em sala de aula em um exemplo real de engenharia.Sendo a pioneira do projeto no Brasil, com mais de 25 anos de histria a Equipe detm 8 ttulos nacionais (nenhuma outra equipe brasileira possui mais ttulos nacionais do que a EESC USP Baja SAE), sendo a atual bicampe nacional e atual campe regional. Desde sua fundao mantm um compromisso de sempre manter a competitividade e inovao, buscando estar sempre entre as melhores equipes do Brasil e do Mundo."url = "img3"></CardExtras>
                <CardExtras title = "Campanha USP do Agasalho - Projetos em Engenharia de Computao"  text = "A Campanha USP do Agasalho  um projeto social voluntrio formado por alunos das universidades de So Carlos. O projeto arrecada e doa roupas para mais de 40 instituies carentes de So Carlos e regio. Alm disso, por meio de visitas e atividades sociais, visa diminuir a distncia entre a universidade e a comunidade, aproximando todas as pessoas envolvidas com o projeto, de forma a gerar um impacto social positivo para todos. Venha conhecer mais sobre a Campanha nas palestras de apresentao: dia 04/03 (janta) e 05/03 (almoo)!"url = "img4"></CardExtras>
                <CardExtras title = "EESC jr."  text = "A EESC jr.  a Empresa jnior de Engenharia e Arquitetura do campus. Aqui vivenciamos o dia-a-dia de uma empresa de consultoria j na graduao. Muito alm disso, temos como nosso propsito tornar o Brasil um pas mais empreendedor com a misso de Transformar Vidas por meio de Engenharia e Arquitetura. Atualmente executamos projetos em 6 reas: Ambiental, Arq/Civil, Materiais, Mecnica/Mecatrnica, Produo e Tecnologia e tambm contamos com a rea de backoffice; comercial (vendas e marketing), inovao e gesto (pessoas/performance e jurdico/financeiro). Vem conhecer mais sobre o nosso time!"url = "img5"></CardExtras>
                <CardExtras title = "EESC-USP Mileage"  text = "Desenvolvemos veculos de alta eficincia energtica e veculos autnomos, visando sempre tecnologia e sustentabilidade."url = "img6"></CardExtras>
                <CardExtras title = "EESC USP- Formula SAE"  text = "A EESC jr.  a Empresa jnior de Engenharia e Arquitetura do campus. Aqui vivenciamos o dia-a-dia de uma empresa de consultoria j na graduao. Muito alm disso, temos como nosso propsito tornar o Brasil um pas mais empreendedor com a misso de Transformar Vidas por meio de Engenharia e Arquitetura. Atualmente executamos projetos em 6 reas: Ambiental, Arq/Civil, Materiais, Mecnica/Mecatrnica, Produo e Tecnologia e tambm contamos com a rea de backoffice; comercial (vendas e marketing), inovao e gesto (pessoas/performance e jurdico/financeiro). Vem conhecer mais sobre o nosso time!"url = "img7"></CardExtras>
                <CardExtras title = "EESC-USP AeroDesign"  text = "A EESC jr.  a Empresa jnior de Engenharia e Arquitetura do campus. Aqui vivenciamos o dia-a-dia de uma empresa de consultoria j na graduao. Muito alm disso, temos como nosso propsito tornar o Brasil um pas mais empreendedor com a misso de Transformar Vidas por meio de Engenharia e Arquitetura. Atualmente executamos projetos em 6 reas: Ambiental, Arq/Civil, Materiais, Mecnica/Mecatrnica, Produo e Tecnologia e tambm contamos com a rea de backoffice; comercial (vendas e marketing), inovao e gesto (pessoas/performance e jurdico/financeiro). Vem conhecer mais sobre o nosso time!"url = "img8"></CardExtras>
                <CardExtras title = "Efestus"  text = "A EESC jr.  a Empresa jnior de Engenharia e Arquitetura do campus. Aqui vivenciamos o dia-a-dia de uma empresa de consultoria j na graduao. Muito alm disso, temos como nosso propsito tornar o Brasil um pas mais empreendedor com a misso de Transformar Vidas por meio de Engenharia e Arquitetura. Atualmente executamos projetos em 6 reas: Ambiental, Arq/Civil, Materiais, Mecnica/Mecatrnica, Produo e Tecnologia e tambm contamos com a rea de backoffice; comercial (vendas e marketing), inovao e gesto (pessoas/performance e jurdico/financeiro). Vem conhecer mais sobre o nosso time!"url = "img9"></CardExtras>
                <CardExtras title = "Enactus USP - So Carlos"  text = "A Enactus  uma organizao sem fins lucrativos presente em 36 pases que une estudantes, lderes executivos e acadmicos visando ao empoderamento de comunidades em vulnerabilidade socioeconmica atravs do Empreendedorismo Social.Nosso time faz parte dessa rede e atualmente conta com 4 projetos na cidade de So Carlos, buscando mudar a realidade ao nosso redor e impactar a sociedade nos pilares social, econmico e ambiental.Buscamos tornar estudantes e comunidades agentes de mudana na sociedade por meio da ao empreendedora sustentvel, compartilhando oportunidades e impactando vidas.Para saber mais sobre nosso time e nossos projetos, assista a uma de nossas Palestras de Apresentao (dias 02 a 05 de maro) e acesse nosso Facebook, Instagram ou site (enactususpsaocarlos.com)"url = "img10"></CardExtras>
                <CardExtras title = "Fellowship of the Game"  text = "Fellowship of the Game  um grupo de desenvolvimento de jogos associado ao Instituto de Cincias Matemticas e Computao - ICMC. Nosso objetivo  complementar o currculo acadmico atravs da produo de jogos e da organizao de eventos gratuitos e abertos para a comunidade como minicursos e a USP Game Link. Realizamos tambm Gamenights para integrar as pessoas do nosso instituto!Como um desenvolvedor de jogos voc poder aprender a programar, desenhar Artes 2D e 3D, criar roteiros, desenvolver Game Design, realizar publicao de jogos em plataformas, criar msica, ter liderana, realizar eventos e muito mais!Para mais informao acesse: fog.icmc.usp.br"url = "img11"></CardExtras>
                <CardExtras title = "Gaperia"  text = "Sejam bem-vindos calouros!Ns somos a torcida dos nossos atletas e com nossos instrumentos botamos o ginsio inteiro  loucura durante os jogos universitrios! No CAASO somos a Gaperia, uma das mais legais baterias do Brasil inteiro, fundadora do maior torneio de Baterias Universitrias do pas, o Interbatuc.Ns torcemos em jogos, apresentamos musicais em festas e formaturas, competimos em torneios e muito mais! Para participar  s acessar o QrCode ao lado, simples n?Esperamos vocs!"url = "img12"></CardExtras>
                <CardExtras title = "Grupo SEMEAR"  text = "O Grupo SEMEAR  uma organizao estudantil da Escola de Engenharia de So Carlos (EESC) da Universidade de So Paulo (USP) voltada para o desenvolvimento de projetos de robtica, desde robs autnomos e drones, at robs de combate e iniciativas sociais de divulgao tcnico-cientfica em engenharia e robtica. Nossa viso  construir um grupo de excelncia em engenharia e de referncia nacional em robtica. A cada ano nos aproximamos desse sonho, venha nos ajudar com isso!"url = "img13"></CardExtras>
                <CardExtras title = "IEEE Student Branch"  text = "O IEEE  uma organizao internacional dedicada  incentivar o uso da tecnologia em prol da sociedade. No ramo de So Carlos, temos a Computer Society, com projetos em Desenvolvimento web/mobile, Inteligncia Artificial aplicada a jogos e viso computacional. Alm de projetos em outras reas como energia renovvel e robtica.Sigam as nossas pginas nas redes sociais @ieeeuspsc"url = "img14"></CardExtras>
                <CardExtras title = "iTeam USP So Carlos"  text = "Entidade destinada a promover a internacionalizao na USP So Carlos. Para tanto, faz a recepo dos intercambistas estrangeiros que vm para So Carlos e os ajuda a se aclimatar mais facilmente  chegada ao Brasil, por meio de programas como o Housing e Buddy Program. O iTeam USP So Carlos tambm realiza eventos em que incentiva os estudantes a buscarem experincias internacionais, seja por meio de intercmbios ou outros meios."url = "img15"></CardExtras>
                <CardExtras title = "Ganesh"  text = "O Ganesh  um grupo de estudo e extenso focado em segurana de informao, atualmente estudamos temas como Criptografia, Pentest, segurana Web e em Redes, Engenharia Reversa em softwares e malwares, Hardware Hacking, alm de outros temas voltados  segurana. Participamos tambm de 'competies hackers', conhecidas como CTFs. Alm disso, o grupo organiza palestras dentro e fora da universidade, e realiza de projetos visando levar conhecimento sobre segurana da informao para diversos pblicos." url = "img16"></CardExtras>
                <CardExtras title = "Liga de Mercado Financeiro - So Carlos"  text = "A Liga de Mercado Financeiro de So Carlos  uma organizao estudantil sem fins lucrativos que visa capacitar seus membros em assuntos diversos do mercado financeiro, organizando palestras e eventos com pessoas experientes no ramo e participando de competies de alto nvel. Desde de 2014, estamos presentes em pdios e em lugares de destaque no meio acadmico, o que tornou a Liga reconhecida pela sua excelncia. Alm disso, os membros participam de diretorias como Ao Social, Marketing, Capacitao, Eventos, Presidncia e da Comisso de Diversidade, realizando atividades e projetos que nos fazem quem somos! Tambm possumos nossos ncleos de estudos nas reas de: Equity Research, Investment Banking, Asset Management, Privaty Equity, Venture Capital e Nivelamento. Para mais informaes, entre no nosso instagram: @lmf_saocarlos ou acesse nosso site: lmf-saocarlos.com"url = "img17"></CardExtras>
                <CardExtras title = "Iniciativas Mercado"  text = "O grupo Iniciativas Mercado  uma Empresa Jnior de eventos que tem como principal objetivo organizar e promover eventos alinhados ao mercado de trabalho, dando a oportunidade de jovens universitrios se desenvolverem pessoal e profissionalmente. Nosso intuito  impactar diretamente os estudantes em nosso meio, para que tenham conhecimento e qualificao para adentrar no mercado. Somos responsveis por organizar a Feira Mercado, o maior evento com a temtica de mercado de trabalho voltado para o pblico universitrio da regio. Alm da Feira Mercado, tambm desenvolvemos outros eventos para ajudar graduandos a conhecerem mais esse cenrio e a se prepararem para o mercado de trabalho, promovendo capacitao e divulgao de informaes."url = "img18"></CardExtras>
                <CardExtras title = "Projeto Semente"  text = "O Projeto Semente visa o aprendizado com tecnologia para crianas em condies de vulnerabilidade social, por meio de aulas de robtica e empreendedorismo. Junto com o Projeto Pequeno Cidado, incentivamos o gosto pela cincia e a insero dos jovens no mundo das inovaes!"url = "img19"></CardExtras>
                <CardExtras title = "TOPUS Projetos Aeroespaciais"  text = "O TOPUS  um grupo extracurricular que projeta e constri foguetes, realiza pesquisas relacionadas ao assunto, organiza eventos sociais e participa de competies. Prezamos pela expanso do setor aeroespacial brasileiro e pelos positivos impactos sociais que podemos causar na sociedade a nossa volta por meio de integrao e distribuio dos nossos conhecimentos.A utilizao das placas de Arduno embarcadas no foguete requer uma equipe preparada e disposta a aprender sempre. Esse ano estamos com projetos maiores que demandam mais sensores e programaes mais complexas. Portanto, alunos que gostem de impresso de circuitos, programao ou foguetes em geral so bem vindos no nosso Processo seletivo! Para acompanhar o PS, siga nossa pgina no facebook"url = "img20"></CardExtras>
                <CardExtras title = "Tup"  text = "Fundada em 2012, a Equipe EESC/USP Tup  uma equipe de frmula estudantil que projeta e constri carros eltricos de corrida visando competir na Frmula SAE, maior competio estudantil do mundo. Visando ser exemplo de desempenho e precursores na mobilidade limpa, nos apresentamos como uma oportunidade para os alunos da USP de desenvolver habilidades como trabalho em equipe, organizao, gesto, administrao e liderana. Alm disso a aprendizagem de conhecimentos tcnicos como manufatura, desenho e simulao em softwares 3D, usinagem, seleo de materiais, montagem de circuitos eletrnicos e programao. Gostou? Venha fazer histria e trabalhar com o Tup. Para nos conhecer melhor, acesse: https://www.facebook.com/usp.tupa"url = "img21"></CardExtras>
                <CardExtras title = "Warthog Robotics"  text = "O Warthog Robotics  um grupo de pesquisa, desenvolvimento e extenso em Robtica Mvel da USP - So Carlos. Formado por cerca de 140 alunos de graduao e ps-graduao de diversos cursos do campus. As atividades desenvolvidas englobam reas como: Eletrnica Embarcada, Mecnica, Materiais, Software, Viso Computacional, Inteligncia Artificial, Gerenciamento de Projetos, Gesto de Pessoas, Marketing, entre outras. Aplicamos nossos projetos em competies de futebol de robs, robs assistenciais e combate de robs, obtendo grandes resultados!"url = "img22"></CardExtras>
                <CardExtras title = "Women in Tech - WiT"  text = "Diante da alta evaso de mulheres em cursos os quais so minoria, a maior parte deles ligados  tecnologia, o Women in Tech tem como objetivo realizar eventos, reunies e debates com a finalidade de discutir a falta de representatividade feminina nas reas de cincia e tecnologia, assim como incentivar mais mulheres a ingressarem na rea."url = "img23"></CardExtras>
                <CardExtras title = "Yanagi Taiko"  text = "Diante da alta evaso de mulheres em cursos os quais so minoria, a maior parte deles ligados  tecnologia, o Women in Tech tem como objetivo realizar eventos, reunies e debates com a finalidade de discutir a falta de representatividade feminina nas reas de cincia e tecnologia, assim como incentivar mais mulheres a ingressarem na rea."url = "img24"></CardExtras>
                <CardExtras title = "Zenith Aerospace"  text = "O Zenith EESC-USP  um grupo extracurricular da Escola de Engenharia de So Carlos fundado no ano de 2014, com o objetivo de desenvolver e difundir tecnologias existentes no setor aeroespacial nacional. Formado por alunos de diversos cursos do campus da USP So Carlos, a equipe do Zenith utiliza do seu encanto pelo espao para desenvolver pesquisas, produtos e servios voltados para esta rea."url = "img25"></CardExtras>
            </Container>
        </ContentSection>
    )

}
export default CardExtraScreen;