import React, {useEffect} from "react";
import {Container, Wrapper} from './styles';

import NavBar from "../../components/NavBar";
import Header from "../../components/Header";
import WhatIs from "../../components/WhatIs";
import Extras from "../../components/Extra";
import Footer from "../../components/Footer";
import { DefaultTitle } from "../Default/styles";



const Home:React.FC = ()=>{
 
    return(
       <Container>
          <NavBar/>
          <Wrapper>
            <Header 
            title="ENGENHARIA DE COMPUTAO"
            text="A Engenharia da Computao tem como objetivo principal o desenvolvimento de hardware, que  a parte fsica dos equipamentos e seus perifricos. No entanto, nessa profisso tambm existe a interface com o software, que so os sistemas, programas e redes computacionais. Neste site voc conhecer tudo sobre o curso na USP em So Carlos."
            url = "img2"
            />
            <WhatIs />
            <Extras 
            title = "EXTRACURRICULARES" 
            text = "Umas das melhores oportunidades que se tem na universidade so as extras curriculares. Alm de gerar muito aprendizado, tambm so timas para socializar. Dentre as diversas, estas foram responsveis pelo desenvolvimento do site, mas voc pode conferir as demais pelo seu manual!"
            />
            <Footer />
          </Wrapper>
       </Container>
    );
}

export default Home;